@import "./variables.scss";

h1,
h2,
h3,
h4,
h5,
h6 {
  text-transform: uppercase;
}
.container {
  width: 1200px;
  margin: auto;
}
.primary_btn {
  background: $primary-color;
  color: #ffffff;
  font-size: 12px;
  text-transform: uppercase;
  border-radius: 6px;
  height: 40px;
  padding: 0px 15px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  &:hover {
    color: #ffffff;
  }
}
